* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  font-family: "Zen Kurenaido", "Prompt", "sans-serif";
  font-weight: 400;
  background-color: rgb(253, 253, 253);
  color: #000;
  text-align: center;
}

/* Main Part */
.flex-container {
  min-height: 100vh;
  max-width: 100vw;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

.flex-main-body {
  flex-grow: 1;  /* To stick footer to the bottom of the webpage and forbidding scrolling x */
  padding-top: 80px;
}

.loading-text {
  margin-top: 10vh;
}

/* Media Query */
@media (max-width: 950px) {
  .flex-main-body {
    padding-top: 50px;
  }
}